import React, {useState, useEffect, forwardRef }  from 'react'
import {  withRouter } from 'react-router-dom';
import axios from 'axios'
import { Form, Button, Row, Col} from 'react-bootstrap';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import MaterialTable,{MTablePagination} from 'material-table';
import {TablePagination} from "@material-ui/core"
import BoQPopup from './BoQPopup'
import { DataGrid, GridToolbar} from '@mui/x-data-grid';
import Dialog from '@mui/material/Dialog';
import Box from '@mui/material/Box';
import DialogContent from '@mui/material/DialogContent';
import CircularProgress from '@mui/material/CircularProgress'
import AddBox from '@mui/icons-material/AddBox';
import ArrowDownward from '@mui/icons-material/ArrowDownward';
import Check from '@mui/icons-material/Check';
import ChevronLeft from '@mui/icons-material/ChevronLeft';
import ChevronRight from '@mui/icons-material/ChevronRight';
import Clear from '@mui/icons-material/Clear';
import DeleteOutline from '@mui/icons-material/DeleteOutline';
import Edit from '@mui/icons-material/Edit';
import FilterList from '@mui/icons-material/FilterList';
import FirstPage from '@mui/icons-material/FirstPage';
import LastPage from '@mui/icons-material/LastPage';
import Remove from '@mui/icons-material/Remove';
import SaveAlt from '@mui/icons-material/SaveAlt';
import Search from '@mui/icons-material/Search';
import ViewColumn from '@mui/icons-material/ViewColumn';

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});



function SubmitBoqSpecificTaskBody(props) {
    
    let [users, setUsers] = useState([]);
    let [companies, setCompanies] = useState([]);
    const [openPopup, setOpenPopup]= useState(false)
    const [boqData, setBoqData] = useState([])
    const [totalCost, setTotalCost] = useState()
    const [approvalHistory,setApproalHistory] = useState([])
    let [severity, setSeverity] = useState("");
    let [alart, setAlart] = useState("");
    const axiosInstance = axios.create({
        baseURL: process.env.REACT_APP_BACKEND_URL,
    })
    
    let [form, setForm] = useState({})
    let [errors, setErrors] = useState({})
    let [open, setOpen] = useState(false);
    let [openDialog, setOpenDialog] = useState(false);

    let setField = (field, value) => {
    setForm({
            ...form,
            [field]: value
        })
        if ( !!errors[field] ) setErrors({
            ...errors,
            [field]: null
        })
    }

    const tableIcons = {
        Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
        Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
        Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
        Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
        DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
        Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
        Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
        Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
        FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
        LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
        NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
        PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
        ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
        Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
        SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
        ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
        ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
      };
    
    const findFormErrors = () => {
        const {taskCommencedComment} = form
        const newErrors = {}
        // name errors
        //if ( !taskCommencedComment || taskCommencedComment === '' ) newErrors.taskCommencedComment = 'cannot be blank!'


        return newErrors
    }

    const columns = [  
        { title: 'ERP Code', field: 'erpCode', filtering: false, editable: false, cellStyle: {width: 100,minWidth: 100,maxWidth:100, height:1}},
        { title: 'Rate Card Div', field: 'rateCardDiv', editable: false, cellStyle: {width: 100,minWidth: 100,maxWidth:100, height:1}},
        { title: 'Item Discreption', field: 'itemDiscreption', filtering: false, editable: false, cellStyle: {width: 350,minWidth: 350,maxWidth:350, height:1}},
        { title: 'UoM', field: 'itemUom', editable: false,cellStyle: {width: 50,minWidth: 50,maxWidth:50, height:1} },    
        { title: 'Unit Rate', field: 'itemRate', editable:false, filtering: false,cellStyle: {width: 100,minWidth: 100,maxWidth:100, height:1}},
        { title: 'Quantity', field: 'itemQty',cellStyle: {width: 50,minWidth: 50,maxWidth:50, height:1}},
        { title: 'Item Total', field: 'itemTotal', type: 'currency', currencySetting: { currencyCode: 'LKR' }, editable: false, cellStyle: {width: 100,minWidth: 100,maxWidth:100, height:1}},
        { title: 'Comment', field: 'itemComment', editable: "onUpdate", cellStyle: {width: 100,minWidth: 100,maxWidth:100, height:1}},
        
      ];

      //Approal History download table
    const columns_AH = [
        { field: 'taskStatus', headerName: 'Status', width: 250  },
        { field: 'performedDate', headerName: 'Performed Date', width: 200, valueGetter: (params) => params.row.performedDate.split('T')[0]  },
        { field: 'performedBy', headerName: 'Performed by', width: 200, valueGetter: (params) => params.row.performedBy.name  },
        { field: 'comment', headerName: 'Comment', width: 500  }
        
        ];

      function getBoqTotal() {
        var total=0;
        for (let i = 0; i < boqData.length; i++) {     
         total = total + boqData[ i ].itemRate*boqData[ i ].itemQty     
        }
        setTotalCost(total) 
        return total;
      }

      const addSelectedItems = function ( items ) {    
        var boq = [...boqData]    
        items.map( (item) => {              
            const obj = {          
              erpCode: item.erpCode,
              rateCardDiv:item.rateCardDiv,
              itemDiscreption: item.itemDiscreption,
              itemRate: item.itemRate.toFixed(2),
              itemUom: item.itemUom,
              itemQty: 1,
              area: 'Colombo',
              itemTotal:  item.itemRate.toFixed(2),
          }
          boq.push(obj)     
        })
        setBoqData(boq)    
      }

    
    useEffect(() => {
        
        
            // setAcceptedDate(props.location.data.row.taskHistory.find(x=>x.taskStatus==='Task accepted').performedDate)
        // console.log(props.location.data)
        setUsers([{name:props.location.data.row.assignedMobitelOfficer.name}])
        setCompanies([{companyName:props.location.data.row.assignedSubcon.companyName}])
        setForm({
                'taskRef':props.location.data.row.taskRef,
                'siteID':props.location.data.row.siteID,
                'siteName':props.location.data.row.siteName,
                'actualTaskStartDate':props.location.data.row.taskHistory.find(x=>x.taskStatus==='Task commenced').performedDate.split('T')[0],
                'actualTaskCompletedDate':props.location.data.row.taskHistory.find(x=>x.taskStatus==='Task completed').performedDate.split('T')[0],
                'taskAssignedDiv':props.location.data.row.taskAssignedDiv,
                'assignedMobitelOfficer': props.location.data.row.assignedMobitelOfficer.name,
                'workScope':props.location.data.row.workScope,
                'assignedSubcon': props.location.data.row.assignedSubcon.companyName
            })
        setApproalHistory(props.location.data.row.taskHistory.filter((e)=>(e.taskStatus==="BOQ Verified_0")||(e.taskStatus==="BOQ Verified_1")||(e.taskStatus==="BOQ Verified_2")||(e.taskStatus==="BOQ Rejected")||(e.taskStatus==="BOQ submitted"))) 
        if(props.location.data.row.taskStatus==="BOQ Rejected"){
            setBoqData(props.location.data.row.boqs[props.location.data.row.boqs.length-1].boqData)
        setTotalCost(props.location.data.row.boqs[props.location.data.row.boqs.length-1].totalCost)
        }
        
        

        
    } , [ ] )


    const smsFetch = async (mobileNo) => {
        //console.log(users.filter((obj) => (obj._id === form.assignedMobitelOfficer))[0].mobileNo)
        const url = "https://msmsenterpriseapi.mobitel.lk/EnterpriseSMSV3/esmsproxyURL.php"
        const Body ={
            "username" : "smsdigisvc_sci", 
            "password" : "Sci@#25", 
            "from": "Mobitel-PMS", 
            "to": "0"+mobileNo, 
            "text": "You have new BOQ to review. Please login to https://projectonline.mobitel.lk/",
            "mesageType":0
        }
        //console.log(JSON.parse(localStorage.getItem("user")))
        const requestOptions = {
            method: 'POST',
            body: JSON.stringify(Body),
            mode: 'no-cors'
        };
        const response = await fetch(url,requestOptions);
        // console.log(response)

    };

    const onAccept=(e)=> {
        e.preventDefault()

        const newErrors = findFormErrors()
        // Conditional logic:
        if ( Object.keys(newErrors).length > 0 ) {
            // We got errors!
            setErrors(newErrors)
        } else {
            const taskObject = {
                taskStatus: 'BOQ submitted'
            };

            // console.log(form.assignedMobitelOfficer)

            axiosInstance.put('projonline/update/'+props.location.data.row._id, {totalCost,boqData,taskObject,taskHistory:{taskStatus: 'BOQ submitted', comment: form.boqSubmittedComment}},{headers:{token:"Bearer "+ JSON.parse(localStorage.getItem("user")).accessToken}})
                .then((res) => {
                    // console.log(res.data)
                    
                    smsFetch(props.location.data.row.assignedMobitelOfficer.mobileNo)

                    setOpenDialog(true)

                    setTimeout(() => {
                        setAlart(res.data.alart)
                        setSeverity(res.data.severity)
                        setOpen(true)
                    }, 2000)
                    
                    setTimeout(() => {
                        setOpenDialog(false)
                        setOpen(false)
                        props.history.push({pathname:'/submitBoqTasks'})
                    }, 5000)

                }).catch((error) => {
                    console.log(error)
                    
                });
            }

    }

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
    
        setOpen(false);
    };

    
    

    return (
        <div>
            
                        {/* Content Wrapper. Contains page content */}
            <div className="content-wrapper">
            {/* Content Header (Page header) */}
            <div className="content-header">
            <section className="content-header">
                    <div className="container-fluid">
                    <div className="row mb-2">
                        <div className="col-sm-6">
                        <h1>Sumbit BOQ Task ID: {form.taskRef} </h1>
                        </div>
                    </div>
                    </div>{/* /.container-fluid */}
                </section>
            </div>
            {/* /.content-header */}
            {/* Main content */}
            <section className="content">
                <div className="container-fluid">
                <Form >
                    
                    <Row>
                        <Form.Group as={Col} >
                            <Form.Label>Site ID</Form.Label>
                            <Form.Control disabled value = {form.siteID} className="col-md-9" type="text" placeholder="Enter site ID" isInvalid={ !!errors.siteID }/>
                            <Form.Control.Feedback type='invalid'>
                                { errors.siteID }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                            <Form.Label>Site Name</Form.Label>
                            <Form.Control  disabled value = {form.siteName} type="text" placeholder="Enter site Name" isInvalid={ !!errors.siteName }/>
                            <Form.Control.Feedback type='invalid'>
                                { errors.siteName }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} controlId="">
                        </Form.Group>

                    </Row>
                    

                    <Row className="mb-3">
                        

                        <Form.Group as={Col} >
                        <Form.Label>Actual Work Commencement Date</Form.Label>
                            <Form.Control disabled value = {form.actualTaskStartDate}  className="col-md-9" type="date" isInvalid={ !!errors.actualTaskStartDate }/>
                            <Form.Control.Feedback type='invalid'>
                                { errors.actualTaskStartDate }
                            </Form.Control.Feedback>
                            
                        </Form.Group>

                        <Form.Group as={Col} >
                            <Form.Label>Actual Work Completed Date</Form.Label>
                            <Form.Control disabled value = {form.actualTaskCompletedDate}  className="col-md-9" type="date" isInvalid={ !!errors.actualTaskCompletedDate }/>
                            <Form.Control.Feedback type='invalid'>
                                { errors.actualTaskCompletedDate }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                        </Form.Group>

                    </Row>
                    <Row>
                        <Form.Group as={Col} >
                            <Form.Label>Task Assigned Division</Form.Label>
                            <Form.Control disabled value = {form.taskAssignedDiv} as="select" defaultValue="Choose..." isInvalid={ !!errors.taskAssignedDiv }>
                                <option>Choose...</option>
                                <option>Project_Radio</option>
                                <option>Project_Core</option>
                                <option>Project_TX</option>
                                <option>Project_IBS</option>
                                <option>Project_WiFi</option>
                            </Form.Control>
                            <Form.Control.Feedback type='invalid'>
                                { errors.taskAssignedDiv }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                            <Form.Label>Assigned Mobitel Officer</Form.Label>
                            <Form.Control disabled as="select" value = {form.assignedMobitelOfficer} defaultValue="Choose..." isInvalid={ !!errors.assignedMobitelOfficer }>
                                <option>Choose...</option>
                                {users.map(opt => (
                                    <option value={opt._id}>{opt.name}</option>
                                ))}
                            </Form.Control>
                            <Form.Control.Feedback type='invalid'>
                                { errors.assignedMobitelOfficer }
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} >
                            <Form.Label>Assigned subcontractor</Form.Label>
                            <Form.Control disabled value={form.assignedSubcon} as="select" defaultValue="Choose..." isInvalid={ !!errors.assignedSubcon } >
                                <option>Choose...</option>
                                {companies.map(opt => (
                                        <option value={opt._id}>{opt.companyName}</option>
                                ))}
                            </Form.Control>
                            <Form.Control.Feedback type='invalid'>
                                    { errors.assignedSubcon }
                            </Form.Control.Feedback>

                        </Form.Group>

                    </Row>
                    
                    <Row>
                        <Form.Group as={Col} >
                            <Form.Label>High level work scope</Form.Label>
                            <Form.Control disabled value={form.workScope}  as="textarea" rows={2}  placeholder="" isInvalid={ !!errors.workScope } />
                            <Form.Control.Feedback type='invalid'>
                                { errors.workScope }
                            </Form.Control.Feedback>
                        </Form.Group>

                    </Row>                 
                                          
                </Form>

                <MaterialTable
                    title={'New BOQ Submission-'+ form.siteID + '_' + form.taskRef}
                    data={boqData}
                    columns={columns}
                    icons={tableIcons}
                    editable={{           
                        onRowDelete:selectedRow=>new Promise((resolve,reject)=>{
                        const index = selectedRow.tableData.id;
                        const updatedRows = [...boqData]                        
                        updatedRows.splice(index, 1)
                        setTimeout(() => {
                          setBoqData(updatedRows)
                          resolve()},
                          2000)
                      }),

                      onRowUpdate:(updatedRow,oldRow)=>new Promise((resolve,reject)=>{
                        const index=oldRow.tableData.id;
                        const updatedRows=[...boqData]      
                        updatedRow.itemTotal = updatedRow.itemRate*updatedRow.itemQty                  
                        updatedRows[index]=updatedRow
                        setTimeout(() => {
                          setBoqData(updatedRows)
                          resolve()
                        }, 2000)
                      })
                      
                  }}
                    options={{
                      exportButton: true,
                      pageSizeOptions: [boqData.length,5, 10, 20],
                      paginationType: 'stepped',
                      showTextRowsSelected: false,
                      actionsColumnIndex: -1,
                      filtering: false,
                      tableLayout: "auto",
                      rowStyle: {
                        backgroundColor: '#EEE'
                    },
                    headerStyle: {
                        backgroundColor: '#01579b',
                        color: '#FFF'
                      }
                    }}
                    components={{
                      Pagination: (props) => (
                        <>
                          <Grid container style={{ pading: 300, background: '#f5f5f5' }}>
                            <Grid sm={9} item>
                              <Typography align='right' variant='subtitle1' fontWeight='bold'>
                                Total
                              </Typography>
                            </Grid>
                            <Grid sm={3} align='right' fontWeight='bold'>
                              {"LKR "+getBoqTotal().toFixed(2)}
                            </Grid>
                          </Grid>
                          <TablePagination {...props}/>
                        </>
                      ),
                    }}
                    actions={[
                      {icon:()=><Button variant="contained">Add</Button>,
                      tooltip:"Add Line Item",
                      onClick:()=>setOpenPopup(true),
                    isFreeAction:true},                    
                    ]}>

                </MaterialTable>

                <br/>
                <Row>
                        <Form.Group as={Col} >
                        <Form.Label>Approval History</Form.Label>
                        <div style={{ height: 250, width: '100%' }}>
                            <DataGrid
                                getRowId={(history)=>history._id}
                                rows={approvalHistory}
                                columns={columns_AH}
                                pageSize={5}
                                rowsPerPageOptions={[5]}
                                rowHeight={30}
                                headerHeight= {40}
                            />
                            
                        </div>
                        </Form.Group>

                    </Row>
                <br />
                <Button variant="primary" onClick={onAccept}>
                            Submit BOQ
                </Button>
                <br />
                </div>
                <br />
                {/* /.container-fluid */}
            </section>
            {/* /.content */}
            
            
            </div>

            <BoQPopup 
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
                acceptedDate= {props.location.data?props.location.data.row.taskHistory.find(x=>x.taskStatus==='Task accepted').performedDate:null}
                callBk = {addSelectedItems}
                > 
            </BoQPopup>

            <Snackbar open={open} autoHideDuration={6000} onClose={handleClose} anchorOrigin={{vertical: "top", horizontal: "center"}}>
                <Alert onClose={handleClose} severity={severity} sx={{ width: '100%' }} >
                    {alart}
                </Alert>
            </Snackbar>
            <Dialog
                open={openDialog}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogContent>
                    <Box sx={{ display: 'flex' }}>
                        <CircularProgress />
                    </Box>

                    
                </DialogContent>
            </Dialog>


        </div>
    )
}

export default withRouter(SubmitBoqSpecificTaskBody)